body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

main {
  flex: 1;
}

.hero-text-shadow {
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7), -2px -2px 5px rgba(0, 0, 0, 0.7);
}

.lead-about {
  font-size: 1.5rem;
  font-weight: 300;
}

.about-section {
  box-shadow: 0 -6px 20px rgba(0, 0, 0, 0.3);
  border-bottom: 1px solid #dee2e6;
}